.products__list {
  display: flex;
  align-items: flex-start;
  margin: 0;
  padding: 0;
  @include mq(mob-only) {
    flex-direction: column;
    padding-top: 148px;
    gap: 148px;
  }
  @include mq(tab-only) {
    flex-wrap: nowrap;
    justify-content: center;
    margin-top: 158px;
    gap: 16px;
  }
  @include mq(desc) {
    flex-wrap: nowrap;
    justify-content: center;
    margin-top: 200px;
    gap: 32px;
  }
}

.products__item {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  border-radius: 24px;
  padding-bottom: 40px;
  @include mq(desc) {
    padding-bottom: 60px;
  }

  &::before {
    content: '';
    display: inline-block;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    margin-top: -50%;
  }

  &:nth-child(1) {
    background-color: var(--background-accent);
  }

  &:nth-child(1)::before {
    content: '';
    @include mq(mob) {
      width: 280px;
      height: 280px;
      background-image: url(../images/products/mob-milkshake-280.webp);
    }
    @media screen and (max-width: 767px) and (min-device-pixel-ratio: 2),
      screen and (max-width: 767px) and (-moz-min-device-pixel-ratio: 2),
      screen and (max-width: 767px) and (-o-min-device-pixel-ratio: 2/1),
      screen and (max-width: 767px) and (-webkit-min-device-pixel-ratio: 2),
      screen and (max-width: 767px) and (min-resolution: 192dpi),
      screen and (max-width: 767px) and (min-resolution: 2dppx) {
      background-image: url(../images/products/mob-milkshake-560@2x.webp);
    }
    @include mq(tab) {
      width: 224px;
      height: 224px;
      background-image: url(../images/products/tab-milkshake-224.webp);
    }
    @media screen and (min-width: 768px) and (min-device-pixel-ratio: 2),
      screen and (min-width: 768px) and (-moz-min-device-pixel-ratio: 2),
      screen and (min-width: 768px) and (-o-min-device-pixel-ratio: 2/1),
      screen and (min-width: 768px) and (-webkit-min-device-pixel-ratio: 2),
      screen and (min-width: 768px) and (min-resolution: 192dpi),
      screen and (min-width: 768px) and (min-resolution: 2dppx) {
      background-image: url(../images/products/tab-milkshake-448@2x.webp);
    }
    @include mq(desc) {
      width: 368px;
      height: 394px;
      background-image: url(../images/products/desk-milkshake-368.webp);
    }
    @media screen and (min-width: 1200px) and (min-device-pixel-ratio: 2),
      screen and (min-width: 1200px) and (-moz-min-device-pixel-ratio: 2),
      screen and (min-width: 1200px) and (-o-min-device-pixel-ratio: 2/1),
      screen and (min-width: 1200px) and (-webkit-min-device-pixel-ratio: 2),
      screen and (min-width: 1200px) and (min-resolution: 192dpi),
      screen and (min-width: 1200px) and (min-resolution: 2dppx) {
      background-image: url(../images/products/desk-milkshake-736@2x.webp);
    }
  }

  &:nth-child(2) {
    background-color: var(--coffee-color);
  }

  &:nth-child(2)::before {
    content: '';
    @include mq(mob) {
      width: 280px;
      height: 280px;
      background-image: url(../images/products/mob-coffee-280.webp);
    }
    @media screen and (max-width: 767px) and (min-device-pixel-ratio: 2),
      screen and (max-width: 767px) and (-moz-min-device-pixel-ratio: 2),
      screen and (max-width: 767px) and (-o-min-device-pixel-ratio: 2/1),
      screen and (max-width: 767px) and (-webkit-min-device-pixel-ratio: 2),
      screen and (max-width: 767px) and (min-resolution: 192dpi),
      screen and (max-width: 767px) and (min-resolution: 2dppx) {
      background-image: url(../images/products/mob-coffee-560@2x.webp);
    }
    @include mq(tab) {
      width: 224px;
      height: 224px;
      background-image: url(../images/products/tab-coffee-224.webp);
    }
    @media screen and (min-width: 768px) and (min-device-pixel-ratio: 2),
      screen and (min-width: 768px) and (-moz-min-device-pixel-ratio: 2),
      screen and (min-width: 768px) and (-o-min-device-pixel-ratio: 2/1),
      screen and (min-width: 768px) and (-webkit-min-device-pixel-ratio: 2),
      screen and (min-width: 768px) and (min-resolution: 192dpi),
      screen and (min-width: 768px) and (min-resolution: 2dppx) {
      background-image: url(../images/products/tab-coffee-448@2x.webp);
    }
    @include mq(desc) {
      width: 368px;
      height: 394px;
      background-image: url(../images/products/desk-coffee-368.webp);
    }
    @media screen and (min-width: 1200px) and (min-device-pixel-ratio: 2),
      screen and (min-width: 1200px) and (-moz-min-device-pixel-ratio: 2),
      screen and (min-width: 1200px) and (-o-min-device-pixel-ratio: 2/1),
      screen and (min-width: 1200px) and (-webkit-min-device-pixel-ratio: 2),
      screen and (min-width: 1200px) and (min-resolution: 192dpi),
      screen and (min-width: 1200px) and (min-resolution: 2dppx) {
      background-image: url(../images/products/desk-coffee-736@2x.webp);
    }
  }

  &:nth-child(3) {
    background-color: var(--green-color);
  }

  &:nth-child(3)::before {
    content: '';
    @include mq(mob) {
      width: 280px;
      height: 280px;
      background-image: url(../images/products/mob-choco-280.webp);
    }
    @media screen and (max-width: 767px) and (min-device-pixel-ratio: 2),
      screen and (max-width: 767px) and (-moz-min-device-pixel-ratio: 2),
      screen and (max-width: 767px) and (-o-min-device-pixel-ratio: 2/1),
      screen and (max-width: 767px) and (-webkit-min-device-pixel-ratio: 2),
      screen and (max-width: 767px) and (min-resolution: 192dpi),
      screen and (max-width: 767px) and (min-resolution: 2dppx) {
      background-image: url(../images/products/mob-choco-560@2x.webp);
    }
    @include mq(tab) {
      width: 224px;
      height: 224px;
      background-image: url(../images/products/tab-choco-224.webp);
    }
    @media screen and (min-width: 768px) and (min-device-pixel-ratio: 2),
      screen and (min-width: 768px) and (-moz-min-device-pixel-ratio: 2),
      screen and (min-width: 768px) and (-o-min-device-pixel-ratio: 2/1),
      screen and (min-width: 768px) and (-webkit-min-device-pixel-ratio: 2),
      screen and (min-width: 768px) and (min-resolution: 192dpi),
      screen and (min-width: 768px) and (min-resolution: 2dppx) {
      background-image: url(../images/products/tab-choco-448@2x.webp);
    }
    @include mq(desc) {
      width: 368px;
      height: 394px;
      background-image: url(../images/products/desk-choco-368.webp);
    }
    @media screen and (min-width: 1200px) and (min-device-pixel-ratio: 2),
      screen and (min-width: 1200px) and (-moz-min-device-pixel-ratio: 2),
      screen and (min-width: 1200px) and (-o-min-device-pixel-ratio: 2/1),
      screen and (min-width: 1200px) and (-webkit-min-device-pixel-ratio: 2),
      screen and (min-width: 1200px) and (min-resolution: 192dpi),
      screen and (min-width: 1200px) and (min-resolution: 2dppx) {
      background-image: url(../images/products/desk-choco-736@2x.webp);
    }
  }
}

.products__name {
  @include fonts(20px, 400, 1.15, 0.04em);
  font-family: 'Titan One';
  text-align: center;
  text-transform: uppercase;
  margin-top: 20px;

  color: #ffffff;

  @include mq(tab) {
    margin-top: 32px;
  }
  @include mq(desc) {
    @include fonts(30px, 400, 1.1333, 0.04em);
  }
}

.products__name::after,
.products__text--pop-up::before {
  content: '';
  display: flex;
  flex-direction: column;
  background-image: url(../images/white-dots.png);
  justify-content: center;
  width: 26px;
  height: 6px;
  margin: 32px auto;
}

.products__text {
  @include fonts(14px, 700, 1.52, 0.04);
  text-align: center;
  padding-left: 40px;
  padding-right: 40px;

  color: #ffffff;

  @include mq(tab) {
    padding-left: 32px;
    padding-right: 32px;
  }
  @include mq(desc) {
    @include fonts(16px, 700, 1.76, 0.04em);
  }

  &.is-hidden {
    height: 1px;
    visibility: hidden;
    opacity: 0;
  }
}
.products__text--pop-up {
  height: 200px;
  transition: opacity 300ms linear, visibility 3000ms linear,
    height 300ms linear;
  @include mq(tab) {
    height: 232px;
  }
}

.products__btn {
  display: flex;
  align-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border-style: none;
  padding: 0;
  margin-top: 32px;

  background-color: #ffffff;
  transition: transform 300ms linear;
  &:hover {
    transform: rotate(90deg);
  }
}
